@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

html {
    background-color: #dad8d8;
    color: #000000;
    font-family: 'Roboto', sans-serif;
    font-size: 62.5%;
    text-align: center;
    line-height: 1.8;
    overflow-x: hidden;
}

.contenedor {
    height: 100vh;
    margin: 0 auto;
    width: 100%;
    
    border-radius: 9px;
    background: linear-gradient(145deg, #c4c2c2, #e9e7e7);
    box-shadow:  14px 14px 27px #b7b5b5,
             -14px -14px 27px #fdfbfb;
}

@media (min-width: 481px)  {
    .contenedor {
        height: 80vh;
        width: 90%;
    }
}

@media (min-width: 768px) {
    .contenedor {
        height: 80vh;
        width: 80%;
    }    
}

.titulos {
    padding: .1rem;
}

h1, h2, .botones {
    width: 90%;
    margin: 2.6rem auto;
}

.titulos h1 {
    display: block;
    font-size: 4rem;
    padding: 2rem 0;
    border-radius: 9px;
    background: #dad8d8;
    box-shadow: inset 48px 48px 96px #a4a2a2,
            inset -48px -48px 96px #ffffff;
}

.titulos h2 {
    display: block;
    font-size: 6rem;
    margin: 0 auto;

    border-radius: 9px;
    background: linear-gradient(145deg, #c4c2c2, #e9e7e7);
    box-shadow:  14px 14px 27px #b7b5b5,
                 -14px -14px 27px #fdfbfb;
}

button {
    font-size: 3.5rem;
    margin: 1rem;
    padding: 2.2rem;
    font-weight: bold;
    border: none; 
    border-radius: 9px;
    background: linear-gradient(145deg, #c4c2c2, #e9e7e7);
    box-shadow:  14px 14px 27px #b7b5b5,
                 -14px -14px 27px #fdfbfb;
}

.botones {
    display: grid; 
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    height: 100%;
}

.uno{
    grid-row-start: 1;
    grid-column-start: 1;
    grid-row-end: 2;
    grid-column-end: 2;
    margin-left: 0;
}

.dos{
    grid-row-start: 2;
    grid-column-start: 1;
    grid-row-end: 3;
    grid-column-end: 3;
    margin-left: 0;
    margin-right: 0;
}

.tres{
    grid-row-start: 1;
    grid-column-start: 2;
    grid-row-end: 2;
    grid-column-end: 3;
    margin-right: 0;
}

@media (min-width: 768px) {
    div .botones {
        display: grid; 

        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        
        gap: 0px;
        height: 100%;
    }
    .uno {
        grid-row-start: 1;
        grid-column-start: 1;
     
        grid-row-end: 2;
        grid-column-end: 2;
    }
    .dos {
        grid-row-start: 1;
        grid-column-start: 2;
     
        grid-row-end: 2;
        grid-column-end: 3;
    }
    .tres {
        grid-row-start: 1;
        grid-column-start: 3;
     
        grid-row-end: 2;
        grid-column-end: 4;
    }
}
